            <p>GitHub repos and site links are provided where possible.</p>
        </section>
        <section class="w-column-wide alt-section">
            <div class="section-part section-text">
                <h1>This Website</h1>
                <p>
                    This website is a portfolio in itself and a single page application built using
                    <a href="https://vuejs.org">Vue.js</a> and
                    <a href="https://fontawesome.com">Font Awesome</a>.
                    All styles are pure CSS using Flexbox.
                    Some images are from <a href="https://pexels.com">Pexels</a>.
                    The font is <a href="https://fonts.google.com/specimen/Lato">Lato</a> by Google Fonts.
                    It is hosted on an Amazon Elastic Compute Cloud (EC2) server.
                </p>
                <p>Last Commit: {{ lastWebsiteCommit.date }} -- "{{ lastWebsiteCommit.message }}"</p>
                <logo-link url="https://github.com/njromano/njromanoSPA"
                           icon="fab fa-github">
                    View on GitHub
                </logo-link>
            </div>
            <div class="section-part">
                <img src="img/home-hero.jpg" />
            </div>
        </section>
        <section class="w-column-wide">
            <div class="section-part">
                <img src="img/boat.jpg" />
            </div>
            <div class="section-part section-text">
                <h1>Marine Tower Systems</h1>
                <p>
                    <a href="http://marine-tower-systems.com">Marine-tower-systems.com</a>
                    is a simple website for the Madisonville, Tennessee fabrication company published
                    in February of 2019. The site uses Bootstrap 4 and provided photos, both from Marine
                    Tower Systems and Pexels.com. Assistance was also given to move the domain provider, including
                    configuring a new CPanel web host. The site was then published via FTP.
                </p>
                <p>Last Commit: {{ lastMTSCommit.date }} -- "{{ lastMTSCommit.message }}"</p>
                <logo-link url="https://github.com/njromano/marine-tower-systems"
                           icon="fab fa-github">
                    View on GitHub
                </logo-link>
                <logo-link url="http://marine-tower-systems.com"
                           icon="fas fa-globe-americas">
                    View on your browser
