        </section>
        <section class="alt-section w-column-wide">
            <div class="section-part section-text">
                <h1>Choosr</h1>
                <p>
                    <a href="https://play.google.com/store/apps/details?id=com.njromano.choosr">Choosr</a>
                    is a native Java Android application created in the latter part of 2015. The app allows users to
                    create lists, pick random results from a list, and mark items in a list as "done" so they no
                    longer can be randomly chosen. Choosr was an exploration in the Android framework at
                    the time, and the project gave much insight into modern application development.
                </p>
                <p>Last Commit: {{ lastChoosrCommit.date }} -- "{{ lastChoosrCommit.message }}"</p>
