        <site-footer />
    </main>
</template>

<script>
    export default {
        name: 'projects',
        data() {
            return {
                lastWebsiteCommit: {},
                lastMTSCommit: {},
                lastChoosrCommit: {}
            }
        },
        created() {
            // get last commits on github projects
            // using axios.spread 
            // see more here: 
            // http://codeheaven.io/how-to-use-axios-as-your-http-client/
            let url = 'https://api.github.com/repos/njromano/';
            axios
                .all([
                    axios.get(url + 'njromanoSPA/commits'),
                    axios.get(url + 'marine-tower-systems/commits'),
                    axios.get(url + 'Randomize/commits')
                ])
                .then(axios.spread((websiteResp, mtsResp, choosrResp) => {
                    // converting to date objects here. may cause not defined errors
                    this.lastWebsiteCommit = {
                        date: new Date(websiteResp.data[0].commit.author.date).toLocaleString(),
                        message: websiteResp.data[0].commit.message
                    };
                    this.lastMTSCommit = {
                        date: new Date(mtsResp.data[0].commit.author.date).toLocaleString(),
                        message: mtsResp.data[0].commit.message
                    };
                    this.lastChoosrCommit = {
                        date: new Date(choosrResp.data[0].commit.author.date).toLocaleString(),
                        message: choosrResp.data[0].commit.message
                    };
                }))
        }
    }
</script>

