<template>
    <div class="hero" 
         :style="heroStyle">
        <slot></slot>
        <div class="hero-text">
            <slot name="hero-text"></slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'hero',
        props: {
            imgPath: String
        },
        computed: {
            heroStyle() {
                return {
                    'background-image': `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('${this.imgPath}')`
                }
            }
        }
    }
</script>

<style scoped>
    .hero {
        height: 600px;
        background-size: cover;
        display: flex;
        flex-direction: column;
    }

    .hero-text {
        display: flex;
        width: 80vw;
        margin: 10vw;
        flex-direction: column;
        color: #E0E0E0;
        justify-content: center;
        flex: auto;
    }

        .hero-text > p {
            margin: 0;
            font-size: 1.25em;
            font-style: italic;
        }
</style>